}

.p-form__radio + span::before {
  display: block;
  content: "";
  position: absolute;
  top: 51%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #000;
  border-radius: 50%;
  width: 1.25rem;
  height: 1.25rem;
}

.p-form__radio + span::after {
  content: "";
  position: absolute;
  top: 51%;
  left: 0.375rem;
  transform: translateY(-50%);
  width: 0.5rem;
  height: 0.5rem;
  background: #000;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.3s ease 0s;
}

.p-form__radio:checked + span::after {
  opacity: 1;
}

