.wp-pagenavi .previouspostslink,
.wp-pagenavi .nextpostslink {
  position: relative;
  color: #fff;
  max-width: 9.375rem;
}
@media screen and (max-width: 767px) {
  .wp-pagenavi .previouspostslink,
  .wp-pagenavi .nextpostslink {
    max-width: 8.75rem;
    font-size: 0.9375rem;
  }
}

.wp-pagenavi .previouspostslink {
  margin-right: auto;
  text-align: left;
  padding-left: 2.25rem;
}
@media screen and (max-width: 767px) {
  .wp-pagenavi .previouspostslink {
    padding-left: 2.5625rem;
  }
}

.wp-pagenavi .nextpostslink {
  text-align: right;
  padding-right: 2.25rem;
  margin-left: auto;
}
@media screen and (max-width: 767px) {
  .wp-pagenavi .nextpostslink {
    padding-right: 2.5625rem;
  }
}